@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0px;
  margin: 0px;
}

.bg{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login-content{
  display: flex;
  flex-direction: column;
  padding: 50px;
  border-radius: 10px;
  align-items: center;
  background-color: #707070;
}
.login-content > h1{
  padding: 10px;
  color: #ffffff;
  font-family: 'Ubuntu', sans-serif;
}
form{
  display: flex;
  flex-direction: column;
  align-items: center;
}
label{
  font-family: 'Ubuntu', sans-serif;
  font-weight: 700;
  color: #FFFFFF;
}
.button-login{
  margin-top: 10px;
  cursor: pointer;
  width: 60%;
  padding: 10px;
  color: #FFFFFF;
  background-color: #8800d6;
  border-radius: 5px;
  border: 0px;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 700;
}
.button-login:hover{
  transition: 0.2s ease-in;
  background-color: #4f007c;
}
.button-login:active{
  background-color: #bdbdbd;
  animation: anime1 0.1s linear;
}
.input-login{
  margin-bottom: 10px;
  padding: 10px;
  min-width: 300px;
  border: 1px solid #cecece;
  outline: none;
  border-radius: 5px;
}
.input-login:focus{
  border: 1px solid #6f00ff;
}
.img-login{
  width: 300px;
}






/* Animações */

@keyframes anime1{
  0%{
    transform: translateY(0px);
  }
  50%{
    transform: translateY(-5px);
  }
  100%{
    transform: translateY(0px);
  }
}
